$red :#ec1b22;

.opex-calculator-container {
  text-align: center;
  border: 1px solid black;
   padding: 40px;


  h2 {
    font-weight: normal;
  }
  h6,
  p {
    margin: 0;
    margin-bottom: 1em;
    margin-top: 0.25em;
  }

  h6 {
    margin-top: 1em;
    line-height: 1em;
  }

  .title {
    margin-bottom: 0.5em;
  }

 

  .input-fields {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 0em;
    margin-bottom: 0em;

    .stats {
      h4,p {
        margin: 0;
      }
      h4{
        margin-top: 1em;
        line-height: 1em;
      }
        display: block;
        border: none;
        outline: none;
        margin-left: 0.5em;
        margin-right: 0.5em;
        padding-left: 1.5em;
        padding-right: 1.5em;
        // height: 3em;
        text-align: center;
    }

    .item-dropdown {
      display: flex;
      flex-direction: row;
      justify-content: center;
      align-items: center;
    }

    .field-item {
      margin-top: 0.5em;

      input {
        border-radius: 100px;
        // display: block;
        background-color: #f1f1f1;
        border: none;
        outline: none;
        // margin-left: 0.5em;
        // margin-right: 0.5em;
        // padding-left: 1.5em;
        // padding-right: 1.5em;
        height: 3em;
        width: 250px;
        text-align: center;
      }

      label {
        margin-top: 0.5em;
        display: block;
      }
    }

    @media only screen and (max-width: 768px) {
      flex-direction: column;
      margin-bottom: 0em;

      .field-item {
        margin-bottom: 0em;
      }
    }
  }



  .red {
    color: $red;
  }

  .selector-container {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin-top: 2em;
    margin-bottom: 2em;

    button {
      cursor: pointer;
      border-radius: 0px;
      display: block;
      background-color: #f1f1f1;
      border: none;
      outline: none;
      // margin-left: 0.5em;
      // margin-right: 0.5em;
      padding-left: 1.5em;
      padding-right: 1.5em;
      height: 3em;
      width: 250px;
      font-weight: bold;

      &.btn {
        background-color: #ec1b22;
        color: white;
        border: 2px solid #ec1b22;
      }

      &.btn {
        background-color: white;
        color: #ec1b22;

        &:hover {
          background-color: #ec1b22;
          color: white;
        }

        &.selected {
          background-color: #ec1b22;
          color: white;
        }
      }
    }

  }

  .input-fields{
    display: flex;
    align-items: center;
    align-items: stretch;
    .fielditem{
      margin-top: 0.5em;
      border-radius: 100px;
      display: block;
      background-color: #f1f1f1;
      border: none;
      outline: none;
      margin-right: 0.5em;
        padding-left: 1.5em;
        padding-right: 1.5em;
      width: 260px;
      display: inline-block;
      font: inherit;
      line-height: 1.5em;
      padding: 0.5em 3.5em 0.5em 1em;
  }

  }
 
}